.comparison__image-wrapper {
  position: relative;
  width: 320px;
  height: 272px;
  box-sizing: border-box;
  margin-bottom: 40px;
}
.comparison__image-wrapper .comparison__image {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 320px;
  height: 100%;
  box-sizing: inherit;
}
.comparison__image-wrapper .foreground-image {
  width: 100%;
  height: 100%;
}
.comparison__image-wrapper .comparison__range {
  position: absolute;
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 100%;
  background: rgba(242, 242, 242, 0.3);
  outline: none;
  margin: 0;
  transition: all 0.2s;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: inherit;
}
.comparison__image-wrapper:hover {
  background: rgba(242, 242, 242, 0.1);
}
.comparison__image-wrapper .comparison__button {
  pointer-events: none;
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: white;
  left: calc(50% - 18px);
  top: calc(50% - 18px);
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: inherit;
}
.comparison__image-wrapper .comparison__button:after {
  content: '';
  padding: 3px;
  display: inline-block;
  border: solid #666666;
  border-width: 0 2px 2px 0;
  transform: rotate(-45deg);
}
.comparison__image-wrapper .comparison__button:before {
  content: '';
  padding: 3px;
  display: inline-block;
  border: solid #666666;
  border-width: 0 2px 2px 0;
  transform: rotate(135deg);
}
